
// Purpose: This file is used to render the FAQ page.
// Kevin's code
function faqTemplate():string{
    return`
    <div class="faqContainer">
        <h1>FAQ</h1>
        <div class="faq">
            <div class="faqQuestion">
                <h2>Frga 1</h2>
                <p>Svar 1</p>
            </div>
            <div class="faqQuestion">
                <h2>Frga 2</h2>
                <p>Svar 2</p>
            </div>
            <div class="faqQuestion">
                <h2>Frga 3</h2>
                <p>Svar 3</p>
            </div>
            </div>
        </div>
    </div>`
}

//Kevin's code
export function renderFaq():void{
    const mainContentContainer = document.querySelector(".mainContent");
    if (!mainContentContainer) {
        return;
    }
    mainContentContainer.innerHTML = "";
    mainContentContainer.innerHTML = faqTemplate();
}