import { isLoggedIn } from "./credentialsComponents/renderLogin";

export function startTemplate(): string  {
    return `
    <div class="startWrapper">
        <h1>Vlkommen till Omnitalk</h1>
        <p>Vlkommen till vrt diskussionsforum! Hr samlas en mngfald av tankar, sikter och ider fr att skapa en berikande och givande dialog. Oavsett om du r hr fr att dela din expertis, lra dig ngot nytt eller bara utforska olika perspektiv, s r detta platsen fr dig. Vi uppmuntrar respektfullt utbyte och ppenhet fr att frmja en atmosfr av lrande och frstelse. S dyk in, engagera dig och lt oss tillsammans utforska vrlden genom diskussion och delat kunnande!</p>
    </div>
`;
}
       

export function renderStart(): void {
    const mainContent = document.querySelector("#mainContent");
    if (!mainContent) return;
    mainContent.innerHTML = "";
    mainContent.innerHTML = startTemplate();
}